'use client';
import { useEffect } from 'react';
import { scopes } from '../utils/spotify';
import querystring from 'querystring';
import { useSearchParams } from 'next/navigation';

export default function Authenticate() {
	const searchParams = useSearchParams();
	const code = searchParams.get('code');

	useEffect(() => {
		if (code) return; // Don't redirect if we have a code

		const state = Math.random().toString(36).substring(7);
		const params = querystring.stringify({
			response_type: 'code',
			client_id: process.env.NEXT_PUBLIC_SPOTIFY_CLIENT_ID,
			scope: scopes.join(' '),
			redirect_uri: process.env.NEXT_PUBLIC_SPOTIFY_REDIRECT_URI,
			state: state,
		});

		const authUrl = 'https://accounts.spotify.com/authorize?' + params;
		window.location.href = authUrl;
	}, [code]);

	if (code) {
		window.location.href = `/my-playlists/?code=${code}`;
		return <div>Redirecting to playlists...</div>;
	}

	return <div>Redirecting to Spotify login...</div>;
}
